export default {
	data() {
		return {
			temFace:""
		}
	},
	methods: {
		// 重新选择 
		changeFace(){
			var me = this
			uni.chooseImage({
				count:1,
				sizeType:["compressed"],
				sourceType:["album"],
				success(res) {
					var tempFilePath = res.tempFilePaths[0];
					me.temFace = tempFilePath
				}
			})	
		},
		// 确认上传
		upload(){
			uni.showLoading({
				title:"没接口 下次再传",
				mask:true,
				success(){
					uni.navigateTo({
						url:"../meInfo/meInfo"
					})
				}
			})

		}
	},
	onLoad(params) {
		var tempFilePath = params.tempFilePath 
		this.temFace = tempFilePath
	}
}
